*, ::after, ::before {
  box-sizing: border-box;
}

html {
  overflow-x: hidden;
  font-size: 18px;
  scroll-behavior: smooth;
  font-family: "Inter", sans-serif;
  color: #6B6B6B;
}

html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, input, textarea, button, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, address, section, video, audio {
  border: 0;
  outline: 0;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
}

img {
  max-width: 100%;
}

a img {
  border: none;
}

a {
  text-decoration: none;
  transition: color .3s ease;
}

label {
  display: inline-block;
}

.text-center {
  text-align: center !important;
}

.mb_20 {
  margin-bottom: 20px;
}

.mb_30 {
  margin-bottom: 30px;
}

.form-area {
  border-radius: 15px;
  background-color: #E6E6E6;
  padding: 46px 20px 0;
}

.form-control {
  display: block;
  width: 100%;
  padding: .53rem 15px .55rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #6B6B6B;
  background-color: #EFEFEF;
  background-clip: padding-box;
  border: 1px solid #CBCBCB;
  appearance: none;
  border-radius: 5px;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.form-control::placeholder {
  opacity: 1;
}

.form-text {
  text-align: right;
  font-size: 0.88889rem;
  margin-top: 3px;
}

.form-check {
  min-height: 1.5rem;
  padding-left: 17px;
  display: inline-flex;
  align-items: center;
}

input.form-check-input {
  border-radius: 5px;
  float: left;
  margin: 0 7px 0 -17px;
  width: 26px;
  height: 25px;
  border: 1px solid #CBCBCB;
  background-color: #EFEFEF;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-print-color-adjust: exact;
  color-adjust: exact;
  print-color-adjust: exact;
}

input.form-check-input:checked {
  background-color: #73D412;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'%3e%3cpath fill='none' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round' stroke-width='3' d='m6 10 3 3 6-6'/%3e%3c/svg%3e");
  background-size: 80% auto;
  background-repeat: no-repeat;
  background-position: center;
  border-color: #73D412;
}

.form-check-label {
  font-size: 0.88889rem;
}

@media (min-width: 575px) {
  .form-area {
    padding: 46px 33px;
  }
  .form-control {
    padding: .33rem 15px .36rem;
  }
  .form-text {
    font-size: 0.77778rem;
    color: #969696;
  }
  .form-text a {
    text-decoration: underline;
    color: #969696;
  }
  .form-text a:hover, .form-text a:active {
    text-decoration: none;
  }
  .form-check-label {
    font-size: 0.77778rem;
  }
  input.form-check-input {
    width: 22px;
    height: 21px;
    margin: 0 5px 0 -17px;
  }
}

.btn {
  cursor: pointer;
  display: inline-block;
  font-size: 1.38889rem;
  line-height: 1.2;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  -webkit-appearance: button;
  padding: 15px 73px 18px;
  border-radius: 37px;
  border: 1px solid;
}

.btn-primary {
  background-color: black;
  border-color: black;
  color: white;
}

.btn-primary:hover, .btn-primary:active {
  background-color: #4c4c4c;
}

@media (min-width: 575px) {
  .btn {
    font-size: 1.11111rem;
    padding: 13px 73px 14px;
  }
}

.login-page {
  background-color: #E6E6E6;
  min-height: 450px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-area {
  width: 100%;
}

.login-area .btn-primary {
  width: 100%;
}

.login-area-logo {
  display: inline-block;
  width: 135px;
  height: 40px;
  margin-bottom: 20px;
  background-size: contain;
  background-image: url("../images/common/vicaso_logo_black.png");
}

@media (min-width: 575px) {
  .login-page {
    background-image: url("../images/content/login-bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  .login-area-logo {
    background-image: url("../images/common/vicaso_logo_white.png");
  }
  .login-area {
    max-width: 340px;
  }
  .login-area .btn-primary {
    width: auto;
  }
}
